import React, { useEffect, useState } from "react";
import { Layout, Menu, Input, Spin, message } from "antd";
import { TagOutlined } from "@ant-design/icons";

import "./Home.css";
import useData from "../hooks/useData";
import useForceDirectedGraph from "../hooks/useForceDirectedGraph";
import { Link, useNavigate, useParams } from "react-router-dom";

const { SubMenu } = Menu;
const { Header, Content, Sider } = Layout;
const { Search } = Input;

function Home({ pathname }) {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [queryContent, setQueryContent] = useState("");
  const [updateGraph] = useForceDirectedGraph();
  const query = useData();
  const { glyph } = useParams();
  const navigate = useNavigate();

  const onQueryChange = (e) => {
    setQueryContent(e.target.value);
  };

  useEffect(() => {
    if (!glyph) {
      setQueryContent("");
      return;
    }
    if (glyph !== queryContent) {
      setLoading(true);
      // The block in setTimeout will execute in another thread.
      // Thus, it won't block rendering.
      setTimeout(async () => {
        setQueryContent(glyph);
        const result = await query(glyph);
        setLoading(false);
        setTimeout(() => {
          if (result) {
            setData(result);
          } else {
            navigate("/");
          }
        }, 100);
      }, 400);
    }
  }, [glyph]);

  const onSearch = async (value) => {
    if (!queryContent) {
      return;
    }
    setLoading(true);
    // The block in setTimeout will execute in another thread.
    // Thus, it won't block rendering.
    setTimeout(async () => {
      const result = await query(value);
      setLoading(false);
      setTimeout(() => {
        if (result) {
          setData(result);
          navigate("/" + queryContent);
        } else {
          message.error({
            content: "",
            style: {
              marginTop: !glyph ? "75vh" : "15vh",
            },
          });
        }
      }, 100);
    }, 400);
  };

  useEffect(() => {
    console.log("data:", data);
    updateGraph(data);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data]);

  const beginningLayout = () => {
    return (
      <Layout
        className="beginningLayout"
        style={{ display: !glyph ? undefined : "none" }}
      >
        <img
          className="logo-home"
          src="/CwnWeb2/cwn-logo-main-home.svg"
          alt=" CWN"
        />
        <Search
          className="search"
          size="large"
          placeholder=""
          value={queryContent}
          onChange={onQueryChange}
          onSearch={onSearch}
        />
      </Layout>
    );
  };

  const searchResultLayout = () => {
    return (
      <Layout style={{ display: glyph ? undefined : "none" }}>
        <Layout className="searchSession">
          <Search
            className="search"
            size="large"
            placeholder=""
            value={queryContent}
            onChange={onQueryChange}
            onSearch={onSearch}
          />
        </Layout>
        <Layout style={{ height: "100%" }}>
          <Sider className="site-layout-background sider" width={"33%"}>
            <Menu
              mode="inline"
              // defaultSelectedKeys={["1"]}
              // defaultOpenKeys={["sub1"]}
              style={{ height: "100%", borderRight: 0, overflow: "auto" }}
            >
              {data?.[0]?.children?.map((lemmaNode, i) => (
                <SubMenu
                  key={`lemma${i}`}
                  icon={<TagOutlined />}
                  title={`${lemmaNode.lemma}${lemmaNode.name}`}
                >
                  {lemmaNode.children?.map((senseNode, j) => (
                    <Menu.ItemGroup
                      key={`sense${i}-${j}`}
                      title={senseNode.definition}
                    >
                      {senseNode.examples ? (
                        senseNode.examples.map((example, k) => (
                          <Menu.Item
                            className="wrapText"
                            key={`example${i}-${j}-${k}`}
                          >
                            {`${k + 1}. ` + example}
                          </Menu.Item>
                        ))
                      ) : (
                        <></>
                      )}
                    </Menu.ItemGroup>
                  ))}
                </SubMenu>
              ))}
            </Menu>
          </Sider>
          <Layout style={{ padding: "0 24px 24px" }}>
            <Content className="site-layout-background">
              <div id="graph" />
            </Content>
          </Layout>
        </Layout>
      </Layout>
    );
  };

  return (
    <Layout>
      <Header className="header">
        <Link to="/">
          <img
            className="logo"
            src="/CwnWeb2/cwn-logo-main.svg"
            alt=" CWN"
            style={{ cursor: "pointer" }}
          />
        </Link>
        {/* <div className="logo" /> */}
        <Menu theme="dark" mode="horizontal" defaultSelectedKeys={["2"]}></Menu>
      </Header>
      {beginningLayout()}
      {searchResultLayout()}
      <div className="spinRoot" style={{ display: loading ? "flex" : "none" }}>
        <Spin size="large" spinning={loading} />
      </div>
    </Layout>
  );
}

export default Home;
